@import url('https://fonts.cdnfonts.com/css/do-futuristic');
@import url('https://fonts.cdnfonts.com/css/star-wars');

.CharactersContainer {
 margin: 0;
 background-color: #080808;
 color: #EC9F05;

}

.CharactersContainer h1 {
  font-family: 'Star Wars', sans-serif;
  text-align: center;
}

.CharactersContainer p {
  font-family: 'Star Wars', sans-serif;
  text-align: center;
  font-size: 18px;
}

.MainWrapper {
  display: flex;
 -webkit-box-align: start;
 -ms-flex-align: start;
 align-items: flex-start;
 -ms-flex-wrap: wrap;
 flex-wrap: wrap;
 padding: 50px 0;
 margin: 0 50px;
}

.CharacterWrapper {
 display:flex;
 padding: 5px 0;
 width: 250px;
 overflow: hidden;
 border: 1px solid #EC9F05;
 margin: 0 15px 20px;
}

.detailItem {
 display: block;
 padding: 30px 30px;
 overflow: hidden;
 position: relative;
 font-weight: 300;
 font-size: 18px;

}

.label {
  font-size: 20px;
  font-weight: 400;
}
.value {
  font-family: 'DO Futuristic', sans-serif;
  font-size: 18px;

}

.CharacterWrapper:hover {
  border: 3px solid #EC9F05;
  transform: scale(1.1);
}
